<template>
  <v-card max-width="1000px" class="mx-auto mt-5">
    <v-data-table
      :headers="headers"
      :items="linhasConstantes[turnoSelecionado] || []"
      :hide-default-footer="true"
      disable-pagination
      disable-filtering
      disable-sort
      class="tabela"
      dense
    >
      <template v-slot:top>
        <v-toolbar flat dense>
          <v-tooltip bottom>
            <template v-slot:activator="{ on, attrs }">
              <v-toolbar-title v-on="on" v-bind="attrs">
                Constantes
              </v-toolbar-title>
            </template>
            <span>Definir horários fixos para as aulas de cada professor</span>
          </v-tooltip>
          <v-icon class="ml-2" @click="dialogDelete = true">mdi-delete</v-icon>
          <span class="ml-5"> {{ stringTotalAulasProfessorTurno }} </span>
          <v-spacer />
          <v-col cols="2">
            <v-select
              v-if="turnos != null && turnos.length > 1"
              v-model="turnoSelecionado"
              :items="turnos"
              item-text="name"
              item-value="id"
            />
          </v-col>
        </v-toolbar>
      </template>

      <template v-slot:item="props">
        <tr>
          <td
            v-if="props.item.aula % aulasPorDiaTurnoSelecionado === 0"
            :rowspan="aulasPorDiaTurnoSelecionado"
            class="vertical-text"
          >
            {{
              diasSemana[
                Math.floor(props.item.aula / aulasPorDiaTurnoSelecionado)
              ].name
            }}
          </td>
          <td
            :style="{
              'border-bottom':
                props.item.aula % aulasPorDiaTurnoSelecionado ===
                aulasPorDiaTurnoSelecionado - 1
                  ? '1px solid black !important'
                  : null,
              'border-right': 'thin solid rgba(0, 0, 0, 0.12)',
              'text-align': 'center',
            }"
          >
            {{ (props.item.aula % aulasPorDiaTurnoSelecionado) + 1 }}
          </td>
          <td
            v-for="(sala, index) in salasTurnoSelecionado"
            :key="index"
            :style="{
              'border-bottom':
                props.item.aula % aulasPorDiaTurnoSelecionado ===
                aulasPorDiaTurnoSelecionado - 1
                  ? '1px solid black !important'
                  : null,
              'border-right': 'thin solid rgba(0, 0, 0, 0.12)',
            }"
          >
            <v-edit-dialog ref="editDialog">
              <span v-if="props.item[sala.id] >= 0">{{
                professoresLookup[props.item[sala.id]]
              }}</span>
              <v-icon v-else-if="props.item[sala.id] === -1" medium>
                mdi-close
              </v-icon>
              <template v-slot:input>
                <v-card flat class="pb-3">
                  <v-autocomplete
                    v-model="props.item[sala.id]"
                    item-text="name"
                    item-value="id"
                    @change="changed"
                    append-outer-icon="mdi-delete"
                    :items="
                      props.item.aula % aulasPorDiaTurnoSelecionado ===
                      aulasPorDiaTurnoSelecionado - 1
                        ? autocompleteItems
                        : autocompleteItemsLastClass
                    "
                    @click:append-outer="clearConstant(props.index, sala.id)"
                  />
                  <v-row>
                    <v-btn
                      color="blue darken-1"
                      text
                      @click="applyToRow(props.index, sala.id)"
                    >
                      Aplicar na linha inteira
                    </v-btn>
                  </v-row>
                  <v-row>
                    <v-btn
                      color="blue darken-1"
                      text
                      @click="applyToColumn(props.index, sala.id)"
                    >
                      Aplicar na coluna inteira
                    </v-btn>
                  </v-row>
                  <v-row>
                    <v-btn
                      color="blue darken-1"
                      text
                      @click="applyToDay(props.index, sala.id)"
                    >
                      Aplicar no dia inteiro
                    </v-btn>
                  </v-row>
                  <v-row>
                    <v-btn
                      color="blue darken-1"
                      text
                      @click="applyToDayAllClasses(props.index, sala.id)"
                    >
                      Aplicar no dia em todas salas
                    </v-btn>
                  </v-row>
                  <v-row>
                    <v-btn color="blue darken-1" text @click="closeEditDialog">
                      Fechar
                    </v-btn>
                  </v-row>
                </v-card>
              </template>
            </v-edit-dialog>
          </td>
        </tr>
      </template>
    </v-data-table>
    <v-dialog v-model="dialogDelete" max-width="500px">
      <v-card>
        <v-card-title class="text-h5">
          Excluir todas as constantes desse turno?
        </v-card-title>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="dialogDelete = false"
            >Cancelar</v-btn
          >
          <v-btn color="blue darken-1" text @click="clear">OK</v-btn>
          <v-spacer></v-spacer>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-card>
</template>

<script>
import multipleApplication from "@/mixins/multipleApplication";
import helper from "@/mixins/helper";

export default {
  name: "TabelaConstantes",
  props: ["id", "linhas"],
  mixins: [multipleApplication, helper],

  data: function () {
    return {
      linhasConstantes: {},
      dialogDelete: false,
    };
  },

  computed: {
    headers() {
      const defaultHeaders = [
        {
          text: "Dia",
          width: "10px",
          align: "center",
        },
        {
          text: "Aula",
          value: "aula",
          width: "10px",
          align: "center",
        },
      ];

      return defaultHeaders.concat(
        Array.from(this.salasTurnoSelecionado, (sala) => {
          return { text: sala.name, value: sala.id };
        })
      );
    },
    autocompleteItems() {
      return [
        { id: -2, name: "Qualquer professor  (Padrão)" },
        { id: -1, name: "Manter aula vazia" },
        ...this.professores,
      ];
    },
    autocompleteItemsLastClass() {
      return [
        { id: -2, name: "Qualquer professor  (Padrão)" },
        ...this.professores,
      ];
    },
    professoresLookup() {
      const arr = Array.from(this.professores, (professor) => [
        professor.id,
        professor.name,
      ]);
      return Object.fromEntries(arr);
    },
  },

  mounted() {
    this.load();
  },

  methods: {
    async load(isReload = false) {
      try {
        this.setLoading(isReload);
        this.saved();
        await this.getStructure();
        const linhasConstantes = {};
        this.turnos.forEach((turno) => {
          const idTurno = turno.id;
          linhasConstantes[idTurno] = [];
          for (let i = 0; i < turno.classes * this.numero_dias; i++) {
            const linha = { aula: i };
            const salas_turno = this.salas.filter(
              (sala) => sala.turno === idTurno
            );
            salas_turno.forEach((s) => (linha[s.id] = -2));
            linhasConstantes[idTurno].push(linha);
          }
        });
        const resp = await this.$axios.get("/constantes/" + this.id);
        const { constantes } = resp.data;
        for (let linha of constantes) {
          const indice = linha.dia * linha.aulas_por_dia + linha.aula;
          linhasConstantes[linha.id_turno][indice][linha.id_sala] =
            linha.id_professor !== null ? linha.id_professor : -1;
        }

        this.linhasConstantes = linhasConstantes;
      } catch (e) {
        console.error(e);
      } finally {
        this.clearLoading();
      }
    },
    async save(reload = true) {
      const resp = await this.$axios.post("/constantes", {
        linhasConstantes: this.linhasConstantes,
        id: this.id,
      });
      if (resp.status === 200 && reload) {
        this.load(true);
      }
    },
    closeEditDialog() {
      this.$refs.editDialog.forEach((dialog) => {
        dialog.isActive = false;
      });
    },
    applyToRow(indexRow, idSala) {
      this.changed();
      const value =
        this.linhasConstantes[this.turnoSelecionado][indexRow][idSala];

      for (const sala in this.linhasConstantes[this.turnoSelecionado][
        indexRow
      ]) {
        if (sala === "aula") {
          continue;
        }

        this.linhasConstantes[this.turnoSelecionado][indexRow][sala] = value;
      }
    },
    applyToColumn(indexRow, idSala) {
      this.changed();
      const value =
        this.linhasConstantes[this.turnoSelecionado][indexRow][idSala];
      for (
        let i = 0;
        i < this.linhasConstantes[this.turnoSelecionado].length;
        i++
      ) {
        this.linhasConstantes[this.turnoSelecionado][i][idSala] = value;
      }
    },
    applyToDay(indexRow, idSala) {
      this.changed();
      const value =
        this.linhasConstantes[this.turnoSelecionado][indexRow][idSala];
      const day = Math.floor(indexRow / this.aulasPorDiaTurnoSelecionado);
      for (
        let i = this.aulasPorDiaTurnoSelecionado * day;
        i < this.aulasPorDiaTurnoSelecionado * (day + 1);
        i++
      ) {
        this.linhasConstantes[this.turnoSelecionado][i][idSala] = value;
      }
    },
    applyToDayAllClasses(indexRow, idSala) {
      this.changed();
      const value =
        this.linhasConstantes[this.turnoSelecionado][indexRow][idSala];
      const day = Math.floor(indexRow / this.aulasPorDiaTurnoSelecionado);
      for (
        let i = this.aulasPorDiaTurnoSelecionado * day;
        i < this.aulasPorDiaTurnoSelecionado * (day + 1);
        i++
      ) {
        for (const sala in this.linhasConstantes[this.turnoSelecionado][
          indexRow
        ]) {
          if (sala === "aula") {
            continue;
          }

          this.linhasConstantes[this.turnoSelecionado][i][sala] = value;
        }
      }
    },
    clear() {
      this.changed();
      this.dialogDelete = false;
      for (
        let i = 0;
        i < this.linhasConstantes[this.turnoSelecionado].length;
        i++
      ) {
        for (const sala in this.linhasConstantes[this.turnoSelecionado][i]) {
          if (sala === "aula") {
            continue;
          }

          this.linhasConstantes[this.turnoSelecionado][i][sala] = -2;
        }
      }
    },
    clearConstant(indexRow, idSala) {
      this.changed();
      this.linhasConstantes[this.turnoSelecionado][indexRow][idSala] = -2;
      this.closeEditDialog();
    },
  },
};
</script>

<style scoped>
.tabela:deep() .ultima > * {
  border-bottom: 1px solid black !important;
}

.tabela:deep() td:not(:last-child) {
  border-right: thin solid rgba(0, 0, 0, 0.12);
}

.vertical-text {
  writing-mode: tb-rl;
  transform: rotate(-180deg);
  text-align: center;
  border: none !important;
  border-left: thin solid rgba(0, 0, 0, 0.12) !important;
  border-top: thin solid black !important;
  white-space: nowrap;
}
</style>
